import { useState, useEffect } from 'react';
import { Card, CardContent, CardDescription, CardHeader, CardTitle } from '@/components/ui/card';
import { Button } from '@/components/ui/button';
import { Input } from '@/components/ui/input';
import { Textarea } from '@/components/ui/textarea';
import { Select, SelectContent, SelectItem, SelectTrigger, SelectValue } from '@/components/ui/select';
import { Badge } from '@/components/ui/badge';
import { 
  Brain, 
  Send, 
  Bot, 
  User, 
  AlertTriangle, 
  Info, 
  Phone,
  Languages,
  MessageSquare
} from 'lucide-react';

interface Message {
  id: number;
  type: 'user' | 'bot';
  content: string;
  timestamp: Date;
}

const translations = {
  English: {
    title: "AI Symptom Checker",
    subtitle: "Get preliminary health guidance based on your symptoms",
    chatTitle: "Chat with AI Health Assistant",
    disclaimer: "Important: This AI tool provides preliminary guidance only. Always consult qualified healthcare professionals for proper diagnosis and treatment.",
    initialMessage: "Hello! I'm your AI health assistant. Please describe your symptoms and I'll help provide preliminary guidance. Remember, this is not a substitute for professional medical advice.",
    placeholder: "Describe your symptoms in detail...",
    sendHint: "Press Enter to send, Shift+Enter for new line",
    send: "Send",
    commonSymptoms: "Common Symptoms",
    commonSymptomsDesc: "Click on any of these common symptoms to get started",
    emergencyTitle: "Emergency Situations",
    emergencyDesc: "If you're experiencing any of these symptoms, seek immediate medical attention:",
    callEmergency: "Call Emergency: 102",
    symptoms: [
      "Fever and headache", "Stomach pain", "Cough and cold", "Joint pain", 
      "Skin rash", "Chest pain", "Nausea and vomiting", "Diarrhea", 
      "Dizziness", "Back pain", "Sore throat", "Fatigue", 
      "Shortness of breath", "Muscle pain", "Eye irritation", "Ear pain",
      "Tooth pain", "Anxiety", "Insomnia", "Constipation"
    ],
    emergencySymptoms: [
      "Chest pain or difficulty breathing",
      "Severe allergic reactions",
      "Loss of consciousness",
      "Severe bleeding or trauma",
      "Signs of stroke (sudden numbness, confusion, trouble speaking)"
    ]
  },
  Hindi: {
    title: "  ",
    subtitle: "         ",
    chatTitle: "      ",
    disclaimer: ":                      ",
    initialMessage: "!                       ,        ",
    placeholder: "      ...",
    sendHint: "   Enter ,     Shift+Enter",
    send: "",
    commonSymptoms: " ",
    commonSymptomsDesc: "            ",
    emergencyTitle: " ",
    emergencyDesc: "          ,     :",
    callEmergency: "  : 102",
    symptoms: [
      "  ", " ", "  ", "  ",
      "  ", "  ", "  ", "",
      " ", " ", "  ", "",
      "   ", "  ", "  ", "  ",
      " ", "", "", ""
    ],
    emergencySymptoms: [
      "       ",
      "   ",
      "",
      "   ",
      "   ( , ,   )"
    ]
  },
  Kannada: {
    title: "  ",
    subtitle: "       ",
    chatTitle: "    ",
    disclaimer: ":       .         .",
    initialMessage: "!     .           .       .",
    placeholder: "   ...",
    sendHint: " Enter ,   Shift+Enter",
    send: "",
    commonSymptoms: " ",
    commonSymptomsDesc: "        ",
    emergencyTitle: " ",
    emergencyDesc: "    ,    :",
    callEmergency: " : 102",
    symptoms: [
      "  ", " ", "  ", " ",
      "  ", " ", "  ", "",
      " ", " ", " ", "",
      " ", " ", " ", " ",
      " ", "", "", ""
    ],
    emergencySymptoms: [
      "    ",
      "  ",
      " ",
      "   ",
      "  ( , ,  )"
    ]
  }
};

const SymptomChecker = () => {
  const [messages, setMessages] = useState<Message[]>([]);
  const [currentMessage, setCurrentMessage] = useState('');
  const [selectedLanguage, setSelectedLanguage] = useState<'English' | 'Hindi' | 'Kannada'>('English');
  const [isLoading, setIsLoading] = useState(false);

  const languages = ['English', 'Hindi', 'Kannada'];
  const t = translations[selectedLanguage];

  // Initialize messages when language changes
  useEffect(() => {
    setMessages([
      {
        id: 1,
        type: 'bot',
        content: t.initialMessage,
        timestamp: new Date()
      }
    ]);
  }, [selectedLanguage]);

  const handleSendMessage = () => {
    if (!currentMessage.trim()) return;

    const userMessage: Message = {
      id: Date.now(),
      type: 'user',
      content: currentMessage,
      timestamp: new Date()
    };

    setMessages(prev => [...prev, userMessage]);
    setIsLoading(true);

    // Simulate AI response
    setTimeout(() => {
      const botResponse = generateBotResponse(currentMessage);
      const botMessage: Message = {
        id: Date.now() + 1,
        type: 'bot',
        content: botResponse,
        timestamp: new Date()
      };
      
      setMessages(prev => [...prev, botMessage]);
      setIsLoading(false);
    }, 1500);

    setCurrentMessage('');
  };

  const generateBotResponse = (symptom: string) => {
    const responses = {
      English: {
        'fever': 'Based on your fever symptoms, here are some recommendations:\n\n Stay hydrated with plenty of fluids\n Rest and avoid strenuous activities\n Monitor your temperature regularly\n Consider paracetamol for temperature control\n\n Seek immediate medical attention if fever exceeds 103F (39.4C) or persists for more than 3 days.',
        'headache': 'For headache relief:\n\n Ensure adequate hydration\n Rest in a quiet, dark room\n Apply cold or warm compress\n Check if it\'s related to stress or eye strain\n\n Consult a doctor if headache is severe, sudden, or accompanied by vision changes.',
        'stomach': 'For stomach-related issues:\n\n Stay hydrated with small sips of water\n Avoid solid foods temporarily\n Try BRAT diet (Bananas, Rice, Applesauce, Toast)\n Rest and avoid dairy products\n\n Seek medical help if you experience severe pain, blood in vomit/stool, or signs of dehydration.',
        'cough': 'For cough and respiratory symptoms:\n\n Stay hydrated and use a humidifier\n Try warm water with honey and lemon\n Avoid irritants like smoke\n Get adequate rest\n\n Consult a doctor if cough persists >2 weeks, produces blood, or is accompanied by high fever.',
        'joint': 'For joint pain:\n\n Apply ice for acute pain, heat for chronic pain\n Gentle stretching and movement\n Over-the-counter pain relievers\n Rest the affected joint\n\n See a doctor if joint is swollen, red, warm, or if pain is severe.',
        'fatigue': 'For fatigue:\n\n Ensure adequate sleep (7-9 hours)\n Stay hydrated\n Eat balanced meals\n Light exercise if possible\n\n Consult a doctor if fatigue persists for weeks or is accompanied by other symptoms.',
        'anxiety': 'For anxiety symptoms:\n\n Practice deep breathing exercises\n Try meditation or mindfulness\n Regular physical activity\n Maintain a regular sleep schedule\n\n Seek professional help if anxiety interferes with daily activities or causes panic attacks.',
        'default': 'Based on your symptoms, here are some general recommendations:\n\n Monitor your symptoms closely\n Stay hydrated and get adequate rest\n Maintain good hygiene\n Consider over-the-counter remedies if appropriate\n\n Please consult with a healthcare professional for proper diagnosis and treatment, especially if symptoms worsen or persist.'
      },
      Hindi: {
        'fever': '      ,    :\n\n    \n       \n       \n      \n\n   103F (39.4C)     3           ',
        'headache': '    :\n\n   \n ,     \n     \n             \n\n   ,              ',
        'stomach': '    :\n\n -  \n       \n BRAT  (, , , ) \n       \n\n   ,   /,         ',
        'cough': '     :\n\n       \n        \n        \n   \n\n   2     ,             ',
        'joint': '    :\n\n     ,      \n     \n    \n     \n\n    , ,          ',
        'fatigue': '  :\n\n    (7-9 )\n  \n   \n       \n\n                 ',
        'anxiety': '    :\n\n      \n    \n    \n     \n\n                 ',
        'default': '    ,     :\n\n     \n      \n    \n       \n\n            '
      },
      Kannada: {
        'fever': '    ,   :\n\n   \n      \n    \n    \n\n  103F (39.4C)   3        ',
        'headache': ' :\n\n   \n ,    \n     \n        \n\n  ,       ',
        // Continuation of the Kannada responses
        'stomach': '  :\n\n    \n    \n BRAT  (, , , ) \n      \n\n  , / ,      ',
        'cough': '   :\n\n     \n       \n     \n   \n\n  2   ,        ',
        'joint': ' :\n\n    ,    \n    \n   \n   \n\n  ,      ',
        'fatigue': ':\n\n    (7-9 )\n  \n   \n    \n\n         ',
        'anxiety': ' :\n\n    \n    \n   \n    \n\n           ',
        'default': '   ,    :\n\n   \n      \n   \n    ()\n\n         '
      }
    };

    symptom = symptom.toLowerCase();
    if (symptom.includes('fever')) return responses[selectedLanguage]['fever'];
    if (symptom.includes('headache')) return responses[selectedLanguage]['headache'];
    if (symptom.includes('stomach')) return responses[selectedLanguage]['stomach'];
    if (symptom.includes('cough') || symptom.includes('cold')) return responses[selectedLanguage]['cough'];
    if (symptom.includes('joint') || symptom.includes('pain')) return responses[selectedLanguage]['joint'];
    if (symptom.includes('fatigue') || symptom.includes('tired')) return responses[selectedLanguage]['fatigue'];
    if (symptom.includes('anxiety')) return responses[selectedLanguage]['anxiety'];

    return responses[selectedLanguage]['default'];
  };

  return (
    <div className="p-4 max-w-3xl mx-auto">
      <Card className="mb-4">
        <CardHeader>
          <CardTitle><Brain className="inline mr-2" /> {t.title}</CardTitle>
          <CardDescription>{t.subtitle}</CardDescription>
        </CardHeader>
        <CardContent>
          <div className="flex items-center gap-4 mb-4">
            <Languages />
            <Select value={selectedLanguage} onValueChange={(lang) => setSelectedLanguage(lang as any)}>
              <SelectTrigger className="w-40">
                <SelectValue placeholder="Select Language" />
              </SelectTrigger>
              <SelectContent>
                {languages.map(lang => (
                  <SelectItem key={lang} value={lang}>{lang}</SelectItem>
                ))}
              </SelectContent>
            </Select>
          </div>
          <p className="text-sm text-muted-foreground mb-4"><Info className="inline mr-2" />{t.disclaimer}</p>
        </CardContent>
      </Card>

      <Card className="mb-4">
        <CardHeader>
          <CardTitle><MessageSquare className="inline mr-2" /> {t.chatTitle}</CardTitle>
        </CardHeader>
        <CardContent>
          <div className="space-y-4 max-h-[300px] overflow-y-auto p-2 border rounded bg-muted">
            {messages.map(msg => (
              <div key={msg.id} className={`flex ${msg.type === 'user' ? 'justify-end' : 'justify-start'}`}>
                <div className={`max-w-xs px-4 py-2 rounded-lg ${msg.type === 'user' ? 'bg-primary text-white' : 'bg-white border'}`}>
                  {msg.content}
                </div>
              </div>
            ))}
            {isLoading && <div className="text-muted-foreground text-sm">AI is typing...</div>}
          </div>

          <div className="mt-4">
            <Textarea
              value={currentMessage}
              onChange={(e) => setCurrentMessage(e.target.value)}
              onKeyDown={(e) => {
                if (e.key === 'Enter' && !e.shiftKey) {
                  e.preventDefault();
                  handleSendMessage();
                }
              }}
              placeholder={t.placeholder}
              className="w-full resize-none"
            />
            <p className="text-xs text-muted-foreground mt-1">{t.sendHint}</p>
            <Button onClick={handleSendMessage} className="mt-2" disabled={isLoading}>
              <Send className="mr-2 h-4 w-4" /> {t.send}
            </Button>
          </div>
        </CardContent>
      </Card>

      <Card className="mb-4">
        <CardHeader>
          <CardTitle><Bot className="inline mr-2" /> {t.commonSymptoms}</CardTitle>
          <CardDescription>{t.commonSymptomsDesc}</CardDescription>
        </CardHeader>
        <CardContent className="flex flex-wrap gap-2">
          {t.symptoms.map((symptom, index) => (
            <Badge key={index} onClick={() => setCurrentMessage(symptom)} className="cursor-pointer">
              {symptom}
            </Badge>
          ))}
        </CardContent>
      </Card>

      <Card>
        <CardHeader>
          <CardTitle><AlertTriangle className="inline mr-2" /> {t.emergencyTitle}</CardTitle>
          <CardDescription>{t.emergencyDesc}</CardDescription>
        </CardHeader>
        <CardContent>
          <ul className="list-disc pl-5 mb-4">
            {t.emergencySymptoms.map((symptom, index) => (
              <li key={index}>{symptom}</li>
            ))}
          </ul>
          <Button variant="destructive">
            <Phone className="mr-2 h-4 w-4" /> {t.callEmergency}
          </Button>
        </CardContent>
      </Card>
    </div>
  );
};

export default SymptomChecker;
